<template>
    <Lay :route="route" :arr="arr" :url="url"/>
</template>
<script>
  import Lay from '@/components/common/template';
  import {devicefailuredata} from '@/config/record'

  export default {
    props: ["route"],
    data() {
      return {
        url: '/data/query/devicefailuredata',
        arr: devicefailuredata
        }
    },
    components: {
      Lay
    },
    mounted: function () {},
    methods: {}
  }

</script>



